<template>
    <div class="main-page">
        <div class="headerTag">选择人员</div>
        <div class="orgs"></div>
        <div class="persons">
            <div class="persons-line" v-for="(item,index) in personList" :key="index" @click="chooseItem(item)">
                <p class="name">{{item.Fname}}</p>
                <p class="orgname"> [ {{item.OrgLongName}} ]</p>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    name: "ChoosePerson",
    props: {},
    components: {},

    data() {
        return {
            personList: []
        };
    },
    methods: {
        personQuery() {
            this._api.personQuery().then(res => {
                if (res) {
                    this.personList = res.data
                }
            })
        },
        chooseItem(item) {
            // this.$u.confirm(`请确认选择 [ ${item.Fname} ]`).then(() => {
            window.temFun(item)
            this.$router.back()
            // }).catch(() => { })
        }
    },
    mounted() {
        this.personQuery()
    }

};
</script>

<style lang='scss' scoped>
.orgs {
    width: 100%;
    height: 44px;
    border-bottom: 1px solid #f5f6f7;
    border-top: 1px solid #f5f6f7;
    padding: 0 15px;
}

.persons {
    width: 100%;
    height: calc(100% - 84px);
    padding: 0 15px;

    .persons-line {
        display: flex;
        align-items: center;
        width: 100%;
        height: 44px;
        border-bottom: 1px solid #f5f6f7;
        white-space: nowrap;
        overflow: auto;

        .orgname {
            font-size: 14px;
        }
    }
}
</style>
